<template>
  <div class="user-header">
    <a class="nav-item1"><img @click="back" class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
    <a class="batai">{{title}}</a>
  </div>
</template>
<style lang="scss" scoped>
  @function n($n) {
  @return $n  / 64 + rem;
  };
  .user-header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: n(80);
    background: #fff001;
    display: flex;
    justify-content:space-between;
    line-height: n(80);
    z-index: 999;
    border-bottom: 1px solid #d8d8d8;
  .nav-item1{
    position: absolute;
    left: 0.2rem;
  .game_1{
    width: n(50);
    height: n(50);
  }
  }
  .batai{
    font-size: 14px;
    font-weight: bold;
    width: 100%;
    display: inline-block;
    text-align: center;
  }

  }
</style>
<script>

    export default {
        data() {
            return {
            }
        },
      props:["title"],
      methods:{
        back:function () {
          this.$router.go(-1);
        },
      }
    }
</script>



