<template>
  <div name="theBarBody" class="theBarBody">
    <Head title="吧台"></Head>
    <router-view></router-view>
    <div class="theBarMain">
      <div class="theBarTop">
        <ul>
          <li @click="security">
            <img src="../../assets/images/home/gc_08.png"/>
            <a>积分商城</a>
          </li>
          <li @click="quickPay">
            <img src="../../assets/images/home/gc_06.png"/>
            <a>快速充值</a>
          </li>
          <li @click="security">
            <img src="../../assets/images/thebar/bc_22.png"/>
            <a>爱的代充</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="theBarCenter">
      <ul>
        <li @click="notice">
          <p>
            <img src="../../assets/images/thebar/bc_23.png"/>
            <a>通知中心</a>
            <span v-show="numShow">{{num}}</span>
          </p>
          <img class="theBarR" src="../../assets/images/thebar/gc_28.png"/>
        </li>
        <!--<li @click="security">-->
          <!--<p>-->
            <!--<img src="../../assets/images/thebar/bc_24.png"/>-->
            <!--<a>安全密码</a>-->
          <!--</p>-->
          <!--<img class="theBarR" src="../../assets/images/thebar/gc_28.png"/>-->
        <!--</li>-->
        <li @click="goaddress">
          <p>
            <img src="../../assets/images/thebar/bc_25.png"/>
            <a>地址管理</a>
          </p>
          <img class="theBarR" src="../../assets/images/thebar/gc_28.png"/>
        </li>
        <li @click="set">
          <p>
            <img src="../../assets/images/thebar/bc_26.png"/>
            <a>系统设置</a>
          </p>
          <img class="theBarR" src="../../assets/images/thebar/gc_28.png"/>
        </li>
        <li @click="bind">
          <p>
            <img src="../../assets/images/thebar/gcBind.png"/>
            <a>绑定安全手机</a>
          </p>
          <img class="theBarR" src="../../assets/images/thebar/gc_28.png"/>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  import Head from './head.vue'
  var urlhost = process.env.API_AJAX;
  export default {
    name:"batai",
    data() {
      return {
        num:'',
        numShow:false,
      }
    },
    components: {
      Head,
    },
    mounted:function () {
      var _this = this;
      var userid = getCookie("userId");
      $.ajax({
        type: 'GET',
        url: urlhost+"systemnum?user_id="+userid,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var mes = JSON.parse(response).message;
          var num = mes.sysnum + mes.backnum;
          if(num > 0){

            _this.numShow = true;
            _this.num = num;
          }else{
            _this.numShow = false;
          }
        },
      });
    },
    methods:{
      notice:function () {
        var _this = this;
        _this.$router.push({path:"/batai/notice"})
      },
      security:function () {
        this.$layer.msg("后期制作，敬请期待!")
      },
      quickPay:function () {
        var _this = this;
        _this.$router.push({
          path:"/usercenter/mydiamond"
        })
      },
      address:function () {
        console.log(1)
      },
      goaddress:function () {
        var _this = this;
        _this.$router.push({path:"/usercenter/address"})
      },
      set:function () {
        this.$layer.msg("后期制作，敬请期待!")
      },
      bind:function () {
        var _this = this;
        _this.$router.push({path:"/visit/bind"})
      },

    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .theBarBody{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    background-color: #f2f2f2;
    .theBarMain {
      margin-top: n(80);
      padding-top: 23px;
      width: 100%;
      height: n(336);
      background: url("../../assets/images/thebar/bc_21bg.png");
      background-size: 100% 100%;
      .theBarTop {
        width: 90%;
        height: n(135);
        margin-left: 5%;
        background: #ffffff;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        border-radius: 10px;
        ul {
          width: 100%;
          height: 100%;
          display: inline-flex;
          justify-content: space-around;
          align-items: center;
          li{
            display: inline-flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: n(100);
            height: n(100);
            img{
              width: n(50);
              height: n(50);
            }
          }
        }
      }
    }
    .theBarCenter{
      margin-top: n(-80);
      width: 90%;
      margin-left: 5%;
      ul{
        li{
          margin-top: 10px;
          display: inline-flex;
          justify-content: space-between;
          align-items: center;
          width: 100%;
          height: n(90);
          background: #ffffff;
          -webkit-border-radius: 10px;
          -moz-border-radius: 10px;
          border-radius: 10px;
          p{
            display: inline-flex;
            justify-content: center;
            align-items: center;
            margin-left: n(45);
            img{
              width: n(40);
              height: n(40);
            }
            a{
              margin-left: n(20);
            }
            span{
              margin-left: n(20);
              width: n(40);
              height: n(40);
              background: red;
              text-align: center;
              line-height: n(40);
              -webkit-border-radius: 50%;
              -moz-border-radius: 50%;
              border-radius: 50%;
              color: #ffffff;
            }
          }
          .theBarR{
            margin-right: n(45);
            width: n(20);
            height: n(40);
          }
        }
      }
    }
  }
</style>




